body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #282c34;
  color: white;
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App {
  text-align: center;
}

.content {
  font-size: 18px;
}

.currency-header {
  margin: 20px;
}

.currency-input {
  margin: 20px;
}

.currency-table {
  margin-left: auto;
  margin-right: auto;
}

.currency-table-items {
  text-align: left;
}

.currency-rates {
  margin: 15px;
}

.currency-rates-table {
  margin-left: auto;
  margin-right: auto;
}

.currency-rates-table-items {
  text-align: left;
}

.chart {
  margin-left: auto;
  margin-right: auto;
 
}
